<template>
  <form v-show="!isLoading" @submit.stop.prevent="handleSubmit">
    <div class="form-group">
      <label for="name">Name</label>
      <input
        type="text"
        class="form-control"
        name="name"
        id="name"
        v-model="restaurant.name"
        placeholder="Enter name"
      />
    </div>

    <div class="form-group">
      <label for="categoryId">Category</label>
      <select
        class="form-control"
        name="categoryId"
        id="categoryId"
        v-model="restaurant.categoryId"
        required
      >
        <option value selected disabled>--請選擇--</option>
        <option
          v-for="category in categories"
          :key="category.id"
          :value="category.id"
        >{{category.name}}</option>
      </select>
    </div>

    <div class="form-group">
      <label for="tel">Tel</label>
      <input
        type="text"
        class="form-control"
        name="tel"
        id="tel"
        v-model="restaurant.tel"
        placeholder="Enter tel"
      />
    </div>

    <div class="form-group">
      <label for="address">Address</label>
      <input
        type="text"
        class="form-control"
        name="address"
        id="address"
        v-model="restaurant.address"
        placeholder="Enter address"
      />
    </div>

    <div class="form-group">
      <label for="opening_hours">Opening Hours</label>
      <input
        type="time"
        class="form-control"
        name="opening_hours"
        v-model="restaurant.openingHours"
        id="opening_hours"
        placeholder="Enter opening_hours"
      />
    </div>

    <div class="form-group">
      <label for="description">Description</label>
      <textarea
        class="form-control"
        id="description"
        name="description"
        rows="3"
        v-model="restaurant.description"
      ></textarea>
    </div>

    <div class="form-group">
      <label for="image">Image</label>
      <img
        v-if="restaurant.image"
        :src="restaurant.image"
        alt
        class="d-block img-thumbnail mb-3"
        width="200"
        height="200"
      />
      <input
        type="file"
        class="form-control-file"
        id="image"
        name="image"
        accept="image/*"
        @change="handleFileChange"
      />
    </div>

    <button
      type="submit"
      class="btn btn-primary"
      :disabled="isProcessing"
    >{{ isProcessing ? "處理中..." : "送出" }}</button>
  </form>
</template>

<script>
import adminAPI from "../apis/admin";
import { Toast } from "../utils/helpers";
export default {
  props: {
    isProcessing: {
      type: Boolean,
      default: false
    },
    initialRestaurant: {
      type: Object,
      default: () => ({
        name: "",
        categoryId: "",
        tel: "",
        address: "",
        description: "",
        image: "",
        openingHours: ""
      })
    }
  },
  data() {
    return {
      categories: [],
      restaurant: {
        ...this.initialRestaurant
      },
      isLoading: true
    };
  },
  watch: {
    initialRestaurant(newValue) {
      //console.log("newValue", newValue);
      this.restaurant = {
        ...this.restaurant,
        ...newValue
      };
      //以防餐廳所屬的類別已經被刪除的狀況
      if (
        !this.categories.some(
          category => category.id === this.restaurant.categoryId
        )
      ) {
        this.restaurant.categoryId = "";
      }
    }
  },
  created() {
    this.fetchCategories();
    //console.log("this.initialRestaurant", this.initialRestaurant);
  },
  methods: {
    async fetchCategories() {
      try {
        const { data } = await adminAPI.categories.get();
        this.categories = data.categories;
        this.isLoading = false;
      } catch (error) {
        this.isLoading = false;
        Toast.fire({
          icon: "error",
          title: "無法顯示類別，請稍後再試"
        });
      }
    },
    handleFileChange(e) {
      console.log("e.target.files", e.target.files);
      const { files } = e.target;
      if (files.length === 0) {
        this.restaurant.image = "";
      } else {
        const imageURL = window.URL.createObjectURL(files[0]);
        this.restaurant.image = imageURL;
      }
    },
    handleSubmit(e) {
      if (!this.restaurant.name) {
        Toast.fire({
          icon: "warning",
          title: "請填寫餐廳名稱"
        });
        return;
      }
      if (!this.restaurant.categoryId) {
        Toast.fire({
          icon: "warning",
          title: "請選擇餐廳類別"
        });
        return;
      }
      const form = e.target; // <form></form>
      const formData = new FormData(form);
      // console.log("formData", formData);
      // console.log("formData.entries", formData.entries());
      // for (let [name, value] of formData.entries()) {
      //   console.log(name + ": " + value);
      // }
      this.$emit("after-submit", formData);
    }
  }
};
</script>